.container {
  display: grid;
  width: 100%;
  grid-template-columns: 20% 80%;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
  grid-gap: 20px;
  /* grid-template-columns: 1fr 1fr 1fr; */
  align-self: center;
  justify-content: center;
  /* grid-gap: 15px; */
  /* margin: 20px; */
}

.box {
  /* border: 2px solid green; */
  margin: 20px 20px;
  width: 200px;
  height: 200px;
}

.card {
  /* width: 18rem; */
  margin: 20px;
  /* border-style: hidden; */
  /* font-size: 30px; */
  padding: 0.5em;
  /* color: #ffffff; */
  /* background: black; */
  text-align: center;
}

.list-group {
  list-style: none;
}
.list-group-item {
  list-style: none;
}

.subtitle {
  color: brown;
}

.card-img-top {
  width: 50% !important;
  margin: auto;
  padding: 10px;
}

.header {
  display: flex;
  justify-content: space-around;
  color: white;
  background-color: #b2071d;
  align-items: center;
}
.super {
  display: flex;
  flex-direction: row;
  width: auto;
}

.sidePanel {
  border: 2px solid red;
  background-color: aliceblue;
}

.mainPanel {
  background-color: black;
}

.datePicker {
  margin: 20px auto;
  display: flex;
}

.note {
  color: white;
}

.main {
  display: flex;
}
